<template>
  <div class="hot">
    <div class="bgimg">
      <div class="aaa"></div>
      <div class="bgtitle"></div>
      <div class="day">
        更新日期:
        {{ day | currentday }}
      </div>
    </div>
    <ul>
      <!-- <SongListItem
        v-for="item in hotsongs"
        :key="item.id"
        :item="item"
        @change-current-song="
          $emit('change-current-song', $event);
          $emit('change-current-play-list', hotsongs);
        "
        :currentSongId="currentSongId"
        :playing="playing"
      ></SongListItem> -->
      <SongListItem
        v-for="(item, index) in hotsongs"
        :key="item.id"
        :item="item"
        @change-current-song="
          $emit('change-current-song', item);
          $emit('change-current-play-list', hotsongs);
        "
        :currentSongId="currentSongId"
        :playing="playing"
        :class="{ lt3: index < 3 }"
        >{{ index + 1 }}</SongListItem
      >
    </ul>
  </div>
</template>
<script>
import dayjs from "dayjs";
import SongListItem from "@/components/SongListItem.vue";
export default {
  components: {
    SongListItem,
  },
  props: {
    currentSongId: {
      type: Number,
    },
    playing: Boolean,
  },
  data: function() {
    return {
      hotlist: [],
      hotsongs: [],
      // dayData,
      day: Date.now(),
    };
  },
  filters: {
    currentday(v, str = " MM月DD日") {
      return dayjs(v).format(str);
    },
  },
  created: function() {
    this.axios.get("/toplist").then((res) => {
      // console.log(res);
      this.hotlist = res.data.list[3];
    });
    this.axios.get("/personalized/newsong").then((res) => {
      // console.log(this, res);
      this.hotsongs = res.data.result;
    });
  },
};
</script>

